<template>
  <div class="card border-0 shadow-sm mb-4 bg-light">
    <h5 class="h6 pl-3 pt-3 mb-0">
      <span class="text-muted text-uppercase">Presentación</span>
    </h5>
    <address class="card-body p-3 m-0">
      <p class="p-0 mx-0 my-2" v-if="location">
        <span class="small font-weight-bold">Dirección</span><br />
        <span>{{ location }}</span>
      </p>
      <p class="p-0 mx-0 my-2" v-if="phone">
        <span class="small font-weight-bold">Teléfono</span><br />
        <span>{{ phone }}</span>
      </p>
      <p class="p-0 mx-0 my-2" v-if="mobile">
        <span class="small font-weight-bold">Celular</span><br />
        <span>{{ mobile }}</span>
      </p>
      <p class="p-0 pt-3 mx-0 my-2 border-top">
        <span v-if="city.name"
          ><router-link
            :to="{
              name: 'UCity',
              params: {
                country: country.slug,
                state: state.code,
                city: city.code
              }
            }"
            role="button"
            >{{ city.name }}</router-link
          ></span
        ><span v-if="state.name"
          >,
          <router-link
            :to="{
              name: 'UState',
              params: {
                country: country.slug,
                state: state.code
              }
            }"
            role="button"
            >{{ state.name }}</router-link
          ></span
        ><br v-if="city.name || state.name" />
        <span v-if="country.name"
          ><router-link
            :to="{
              name: 'UCountry',
              params: { country: country.slug }
            }"
            role="button"
            >{{ country.name }}</router-link
          ></span
        >
      </p>
    </address>
  </div>
</template>

<script>
export default {
  name: "Presentation",
  props: {
    city: {
      _id: String,
      code: String,
      name: String
    },
    country: {
      _id: String,
      name: String,
      slug: String
    },
    location: String,
    mobile: String,
    phone: String,
    state: {
      _id: String,
      code: String,
      name: String
    }
  }
};
</script>

<style scoped></style>
